___CSS_LOADER_EXPORT___.push([module.id, `body {
  display: grid;
  grid-template-columns: 250px 1fr;
  height: 100dvh;
  width: 100vw;
  background-color: var(--bg);
  padding: 8px;
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    sans-serif;
}

hr {
  border: none;
  border-bottom: 1px solid var(--border);
}

dialog {
  background-color: var(--bg);
  color: var(--text);
  border: 1px solid var(--border);
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

dialog::backdrop {
  backdrop-filter: blur(2px);
  background-color: rgba(0, 0, 0, 0.7);
}

dialog form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

dialog form > div {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

dialog form > div select {
  background-color: transparent;
  border: 1px solid var(--border);
  color: var(--text);
  padding: 4px;
  border-radius: 8px;
}

dialog form > div > input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--border);
  color: var(--text);
  outline: none;
  padding: 4px;
}
